import React from 'react';
import { FaDiscord, FaFacebook, FaGithub, FaInstagram, FaLinkedin, FaStackOverflow, FaWhatsapp } from 'react-icons/fa';

import styles from './Footer.module.css';

function Footer() {
    return (
        
        <footer className={styles.footer}>
            <ul className={styles.social_list}>
                <li>
                    <FaFacebook />
                </li>
                <li>
                    <FaInstagram />
                </li>
                <li>
                    <FaLinkedin />
                </li>
                <li>
                    <FaGithub />
                </li>
                <li>
                    <FaWhatsapp />
                </li>
                <li>
                    <FaDiscord />
                </li>
                <li>
                    <FaStackOverflow />
                </li>
            </ul>
            <p className={styles.copy_right}>
                <span>Produzido por Jucilene Nunes</span> &copy; 2021
            </p>
        </footer>
    );
}

export default Footer;
